@tailwind base;
@tailwind components;
@tailwind utilities;

@import "slick-carousel/slick/slick.css";
@import "slick-carousel/slick/slick-theme.css";

// Import css files
@import "slick-carousel/slick/slick.css";
@import "slick-carousel/slick/slick-theme.css";

* {
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.17px;
}

:root {
  --toastify-color-light: #fff;
  --toastify-color-dark: #121212;
  --toastify-color-info: #3498db;
  --toastify-color-success: #009700;
  --toastify-color-warning: #f1c40f;
  --toastify-color-error: #FF1414;
  --toastify-color-transparent: rgba(255, 255, 255, 0.7);

  --toastify-icon-color-info: var(--toastify-color-info);
  --toastify-icon-color-success: var(--toastify-color-success);
  --toastify-icon-color-warning: var(--toastify-color-warning);
  --toastify-icon-color-error: var(--toastify-color-error);

  --toastify-toast-width: 320px;
  --toastify-toast-background: #fff;
  --toastify-toast-min-height: 64px;
  --toastify-toast-max-height: 800px;
  --toastify-font-family: sans-serif;
  --toastify-z-index: 9999;

  --toastify-text-color-light: #757575;
  --toastify-text-color-dark: #fff;

  //Used only for colored theme
  --toastify-text-color-info: #fff;
  --toastify-text-color-success: #fff;
  --toastify-text-color-warning: #fff;
  --toastify-text-color-error: #fff;

  --toastify-spinner-color: #616161;
  --toastify-spinner-color-empty-area: #e0e0e0;

  // Used when no type is provided
  // toast("**hello**")
  --toastify-color-progress-light: linear-gradient(
      to right,
      #4cd964,
      #5ac8fa,
      #007aff,
      #34aadc,
      #5856d6,
      #ff2d55
  );
  // Used when no type is provided
  --toastify-color-progress-dark: #bb86fc;
  --toastify-color-progress-info: var(--toastify-color-info);
  --toastify-color-progress-success: var(--toastify-color-success);
  --toastify-color-progress-warning: var(--toastify-color-warning);
  --toastify-color-progress-error: var(--toastify-color-error);
}

body {
  min-height: 100vh;
  margin: 0;
}

.app-container {
  min-height: 100vh;
}

.colored-background {
  margin-left: -2.5rem;
  margin-right: -2.5rem;
  margin-top: -80px;
  padding-top: 80px;
}

.pink-colored-background {
  background-image: linear-gradient(#f796bb 0vh, #f796bb 70vh, #FFFFFF 200vh, #FFFFFF 100%);
}

.blue-colored-background {
  background-image: linear-gradient(#C2F9FF 0vh, #C2F9FF 70vh, #FFFFFF 200vh, #FFFFFF 100%);
}

.yellow-colored-background {
  background-image: linear-gradient(#FFC26E 0vh, #FFC26E 70vh, #FFFFFF 200vh, #FFFFFF 100%);
}

.green-colored-background {
  background-image: linear-gradient(#DFF8D5 0vh, #DFF8D5 70vh, #FFFFFF 200vh, #FFFFFF 100%);
}

.slick-arrow.slick-prev {
  border-bottom: 6px solid;
  border-left: 6px solid;
  transform: rotate(45deg);
  color: #000000;
}

.slick-arrow.slick-next {
  border-bottom: 6px solid;
  border-left: 6px solid;
  transform: rotate(225deg);
  color: #000000;
}

.text-highlight-shadow {
  background-color: black;
  background-image: linear-gradient(black, gray);
}

.MuiButton-root {
  border-radius: 1rem !important;
}

.footer-color {
  background-color: black;
  color: rgba(255, 255, 255, 0.8);
}

.drawer-content {
  width: 70vw;
  max-width: 20rem;
}

@media only screen and (min-width: 1100px) {
  .content-container {
    margin: 0 auto;
    max-width: 50vw;
  }

}

@media only screen and (min-width: 1100px) {
  .image-content-container {
    margin: 0 auto;
    max-width: 30vw;
  }
}

@media only screen and (max-width: 1100px) and (min-width: 600px) {
  .image-content-container {
    margin: 0 auto;
    max-width: 50vw;
  }
}


.content {
  max-width: 58rem;
  margin: 0 auto;
  min-height: calc(100vh - 284px);
}

#chat-input-message {
  padding: .5rem;
}

#chat-input-message + fieldset {
  border-radius: 2rem !important;
  border: 0;
}

.message-list {
  max-height: 20rem;
  overflow-y: scroll;
  overflow-x: hidden;
}

.image-gallery-content .image-gallery-slide .image-gallery-image {
  max-height: calc(20rem) !important;
}

.rce-container-mlist {
  overflow: hidden;
  min-height: 20rem;
}

.rce-mbox-right {
  background-color: hsl(214, 100%, 59%) !important;
  color: white !important;
}

.rce-mbox-right > * > .rce-mbox-time {
  color: white !important;
}

hr {
  display: block;
  height: 1px;
  border: 1px;
  border-top: 2px solid #000000;
  margin: 1em 0;
  padding: 0;
}

.article-max-height {
  max-height: 233px;
}

.article-cover {
  border-radius: 20px;
}

.text-article-header {
  font-size: 28px;
  font-style: normal;
  line-height: normal;
}

.MuiSvgIcon-fontSizeMedium {
  stroke: black;
  stroke-width: 1;
}

.MuiSvgIcon-fontSizeLarge {
  stroke: black;
  stroke-width: .5;
}

.MuiButton-root {
  line-height: normal !important;
  font-weight: 500 !important;
  letter-spacing: 0.5px !important;
}

.MuiButton-outlined {
  border-color: black !important;
  border-radius: 14px !important;
  text-transform: capitalize !important;
  font-size: 16px !important;
  font-style: normal !important;
}

.MuiButton-contained {
  border-color: black !important;
  background-color: black !important;
  border-radius: 14px !important;
  text-transform: capitalize !important;
  font-size: 16px !important;
  font-style: normal !important;
}

.MuiButton-outlined.MuiButton-sizeSmall {
  padding: 6px 16px !important;
  border-width: 2px !important;
}

.MuiButton-contained.MuiButton-sizeSmall {
  padding: 8px 16px !important;
  border-width: 2px !important;
}

.MuiButton-outlined.MuiButton-sizeMedium {
  padding: 11px 21px !important;
  border-width: 2px !important;
}

.MuiButton-contained.MuiButton-sizeMedium {
  padding: 12px 21px !important;
}

.border-3 {
  border-width: 3px;
}

.photo-gallery {
  display: grid;
  grid-gap: .5rem;
  overflow: hidden;
  aspect-ratio: 55 / 40;
}

.photo-gallery {
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(3, 1fr);
}

.photo-gallery.small {
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

.photo-gallery > .img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.photo-gallery > .img-container > img {
  object-fit: cover;
  border-radius: 1rem;
  width: 100%;
  height: 100%;
}

.photo-gallery.small > .img-container > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.photo-gallery:hover .img-container:hover {
  filter: brightness(70%);
}

.photo-gallery.small:hover .img-container:hover {
  filter: brightness(100%);
}

.main-photo {
  grid-area: 1 / 1 / 4 / 6;
}

.alt-photo-1 {
  grid-area: 1 / 6 / 2 / 8;
}

.alt-photo-2 {
  grid-area: 2 / 6 / 3 / 8;
}

.alt-photo-3 {
  grid-area: 3 / 6 / 4 / 8;
}


.MuiTab-root {
  font-weight: 700 !important;
  text-align: left !important;
  padding: 0 !important;
}


@import "~react-image-gallery/styles/scss/image-gallery.scss";

.mySwiper {
  width: 100%;
  height: 100%;
  padding-bottom: 1rem !important;
}

.max-3-lines {
  overflow: hidden;
  display: -webkit-box;
  line-height: 1.2rem;
  -webkit-line-clamp: 3; /* number of lines to show */
  line-clamp: 3;
  -webkit-box-orient: vertical;
}

.category-select-button {
  margin-right: .5rem !important;
  margin-bottom: .25rem !important;
}

.max-two-lines {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2; /* Number of lines to show */
  max-height: 3.6em;
  line-height: 1.8em;
}

.swiper-button-next,
.swiper-container-rtl .swiper-button-prev,
.swiper-button-prev,
.swiper-container-rtl .swiper-button-next {
  color: white !important;
}

.dark-banner-rgb {
  background: #E61A98;
  background: radial-gradient(at right bottom, #E61A98, #656bda);
}

.light-banner-rgb {
  background: linear-gradient(to bottom, #f8f6f6, #f8f6f6);
}


@media screen and (max-width: 900px) {
  .swiper-button-next,
  .swiper-container-rtl .swiper-button-prev,
  .swiper-button-prev,
  .swiper-container-rtl .swiper-button-next {
    display: none !important;
  }
}

img {
  max-width: 100%;
}

.preformatted {
  white-space: pre;
}

.logo-text {
  font-size: 2rem;
  letter-spacing: 1px;
}

.logo-font {
  font-family: 'Sansita One', sans-serif;
}

.MuiChip-outlined {
  border-color: black !important;
  border-radius: 8px !important;
}

.bg-card {
  background: linear-gradient(#232526, #232526);
  background-size: cover;
}

.bg-card-b {
  background: linear-gradient(#454d66 , #454d66);
  background-size: cover;
}
